.header {
    width: 100%;
    height: 60px;
    padding: 16px 32px;
    display: flex;
    justify-content: flex-end;
}

.navigation-links-block {
    display: inline-flex;
    align-items: center;
}

.navigation-link {
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 29px;
    margin-right: 16px;
    color: black;
    text-decoration: none;
}

.burger-menu {
    display: none;
    font-size: 2rem;
}

.burger-menu-open {
    position: relative;
    z-index: 10;
    font-size: 2rem;
}

@media only screen and (max-width: 768px) {
    .burger-menu {
        display: flex;
    }
    .navigation-links-block {
        display: none;
    }
}